header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin-right: 419px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  transition: transform .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

li:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 1024px) {
  .logo {
    margin-right: 243px;
  }
}

main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition: transform .3s;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-left: 3px;
  margin-right: 3px;
}

.product-card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: transform .3s;
  display: flex;
}

.product-card__title:hover {
  color: #34568b;
}

.product-card__product-code {
  color: #616070;
  width: 98px;
  height: 14px;
  margin-top: 4px;
  margin-bottom: 16px;
  margin-right: 68px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.product-card__label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-tag {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}
