@import '../utils/veriables';

main {
  display: grid;
  grid-template-columns: repeat(1, $card-width);
  gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
}

@media (min-width: 488px) {
  main {
    grid-template-columns: repeat(2, $card-width);
  }
}

@media (min-width: 768px) {
  main {
    grid-template-columns: repeat(3, $card-width);
  }
}

@media (min-width: 1024px) {
  main {
    grid-template-columns: repeat(4, $card-width);
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: $card-width;
  padding: 16px;
  display: flex;
  transition: transform $tramsform-time;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    width: 160px;
    height: 134px;
    margin-left: 3px;
    margin-right: 3px;
    margin-top: 16px;
  }

  &__title {
    display: flex;
    font-weight: 500;
    font-size: 12px;
    margin-top: 40px;
    margin-bottom: 0;
    line-height: 18px;
    color: $text-color-1;
    transition: transform $tramsform-time;
  }

  &__title:hover {
    color: #34568b;
  }

  &__product-code {
    width: 98px;
    height: 14px;
    font-weight: 400;
    font-size: 10px;
    margin-right: 68px;
    margin-top: 4px;
    margin-bottom: 16px;
    line-height: 14px;
    color: $text-color-2;
  }

  &__rating {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__reviews {
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;
    margin: 0;
    color: $text-color-1;
  }

  &__price {
    width: 100%;
    display: flex;
    margin-top: 24px;
    align-items: center;
    justify-content: space-between;
  }

  &__label {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $text-color-2;
  }

  &__price-tag {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $text-color-1;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    line-height: 16px;
    border-radius: 5px;
    margin-top: 16px;
    background-color: $button-color;
    font-weight: 700;
    font-size: 14px;
    border: 1px solid $button-color;
    color: $button-text-color;
    text-decoration: none;
    text-transform: uppercase;
  }

  &__button:hover {
    background-color: $button-text-color;
    color: $button-color;
  }
}
